* {
  padding: 0;
  margin: 0;
}
body {
  background-color: rgba(1, 27, 48, 0.829);
}
.Board {
  margin: auto;
  margin-top: 20px;
  width: 600px;
  height: 600px;
  text-align: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  color: white;
  transition: 2s;
}

.Board-table {
  height: 100%;
  width: 100%;
  transition: 1s;
  box-shadow: 3px 3px 3px 0px rgba(0, 0, 0, 0.829),
    -3px -3px 3px 0px rgba(0, 0, 0, 0.829);
  z-index: 2;
}
h1 {
  padding: 0 0 25px 0;
}

td {
  width: calc(100% / 8);
  height: calc(100% / 8);

  color: teal;
  text-align: center;
  background-color: rgba(3, 25, 100, 0.137);
}

td:hover {
  opacity: 0.3;
}
.odd:nth-of-type(2n-3) {
  background-color: rgba(6, 132, 206, 0.644);
}
.even:nth-of-type(2n) {
  background-color: rgba(6, 132, 206, 0.644);
}

.size {
  font-size: 2rem;
  color: black;
}

.White {
  padding: 20px;
  color: White;
}
.Black {
  padding: 20px;
  transform: rotateX(180deg);

  color: Black;
}

.Down {
  transform: rotateZ(180deg);
}
.Up {
}

.GameOver {
  font-size: 60px;
  position: absolute;
  margin: 19% 9%;
  color: rgb(185, 187, 187);
  z-index: 2;
}

.myButton {
  box-shadow: inset 0px 1px 0px 0px #54a3f7;
  background: linear-gradient(to bottom, #007dc1 5%, #0061a7 100%);
  background-color: #007dc1;
  border-radius: 3px;
  border: 1px solid #124d77;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Georgia;
  font-size: 20px;
  padding: 8px 30px;
  margin-bottom: 30px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #154682;
}
.myButton:hover {
  background: linear-gradient(to bottom, #0061a7 5%, #007dc1 100%);
  background-color: #0061a7;
}
.myButton:active {
  position: relative;
  top: 1px;
}
